button {
    padding: 15px;
    color: white;
    font-size: 18px;
    margin: 10px;
    cursor: pointer;
    outline: none;
    border: none;
    padding: 20px;
    margin-inline: 15px;
    color: black;
    font-size: 18px;
    font-weight: 700;
}
button:nth-of-type(1) {
    background-color: rgb(255, 73, 234);
}
button:nth-of-type(2) {
    background-color: rgb(101, 255, 119);
}
span {
    font-size: 20px;
    margin-inline: 15px;
    padding-inline: 15px;
}